/***************************PREDICTOR LIST ADD*****************************************/
  export const addfile = (params) => {
    return async (dispatch) => {
      // dispatch(addPostInit())
      await http.post('/cricket/predictor', params).then((response) => {        
        
        notifySuccessadd(response.data.message)
        dispatch({
          type: 'ADD_FILE',
          data: response.data.message
        })
      }).catch((e) => {
        
        notifyError(e.response.data.message)
      })
    }
  }
  export const footballListAdd = (params) => {
    return async (dispatch) => {
      // dispatch(addPostInit())
      await http.post('/football/predictor', params).then((response) => {        
        
        notifySuccessadd(response.data.message)
        dispatch({
          type: 'PREDICTOR_LIST_ADD',
          data: response.data.data
        })
      }).catch((e) => {
       
        notifyError(e?.message)
      })
    }
  }
/***************************END PREDICTOR LIST ADD*****************************************/ 


